.Counter{
    /* display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;*/
    background-color: gray;
     color: #fff;

    
    width:400px;
    padding: 10px;
    margin-bottom: 10px;
    /* border: 1px solid black; */
    

   
    font-size: 35px;
    text-align: center;
    
}
.Counter__controls > button {
border: none;
padding: 10px 15px;
background-color: teal;
color: #fff;
font-weight: 500;
margin-left: 5px;
margin-right: 5px;
font-family: inherit;
cursor: pointer;
outline: none;
box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
transition: box-shadow 250ms linear;

}

.Counter__controls > button:active {
    box-shadow: 2px 5px 2px 0 rgb(0 0 0 / 0.5);

}
.Counter__value{
    margin-right: 15px;
    margin-left: 15px;
    font-size: 64px;
}